import React, { useState, useEffect } from 'react';
import { useTonWallet } from '@tonconnect/ui-react';
import MainPage from './components/MainPage';
import CreateContract from './components/CreateContract';
import ContractsList from './components/ContractsList';
import Readme from './components/Readme';
import Sweep from './components/Sweep';
import './App.css';

function applyTelegramTheme(tg) {
    const p = tg.themeParams;
    const root = document.documentElement;

    if (p.bg_color) root.style.setProperty('--tg-theme-bg-color', p.bg_color);
    if (p.secondary_bg_color) {
        root.style.setProperty('--tg-theme-secondary-bg-color', p.secondary_bg_color);
        root.style.setProperty('--tg-input-bg', p.secondary_bg_color);
    }
    if (p.bg_color) root.style.setProperty('--tg-header-bg', p.bg_color);
    if (p.text_color) root.style.setProperty('--tg-theme-text-color', p.text_color);
    if (p.hint_color) root.style.setProperty('--tg-theme-hint-color', p.hint_color);
    if (p.link_color) root.style.setProperty('--tg-theme-link-color', p.link_color);
    if (p.button_color) root.style.setProperty('--tg-theme-button-color', p.button_color);
    if (p.button_text_color) root.style.setProperty('--tg-theme-button-text-color', p.button_text_color);
    if (p.accent_text_color) root.style.setProperty('--tg-theme-accent-color', p.accent_text_color);
    if (p.destructive_text_color) root.style.setProperty('--tg-theme-destructive-color', p.destructive_text_color);
    if (p.section_bg_color) {
        root.style.setProperty('--tg-card-bg', p.section_bg_color);
    } else if (p.bg_color) {
        root.style.setProperty('--tg-card-bg', p.bg_color);
    }
    if (p.section_separator_color) {
        root.style.setProperty('--tg-input-border', p.section_separator_color);
        root.style.setProperty('--tg-card-border', p.section_separator_color);
    }
}

function App() {
    const wallet = useTonWallet();
    const [user, setUser] = useState(null);
    const [currentPage, setCurrentPage] = useState('main'); // 'main', 'create', 'contracts'
    const [pendingContracts, setPendingContracts] = useState([]);

    // Initialize Telegram WebApp
    useEffect(() => {
        const tg = window.Telegram?.WebApp;
        if (!tg) return;

        tg.ready();
        tg.expand();
        applyTelegramTheme(tg);

        const onThemeChanged = () => applyTelegramTheme(tg);
        const onViewportChanged = () => {
            document.documentElement.style.setProperty(
                '--tg-viewport-height',
                `${tg.viewportHeight}px`
            );
        };

        tg.onEvent('themeChanged', onThemeChanged);
        tg.onEvent('viewportChanged', onViewportChanged);
        onViewportChanged();

        return () => {
            tg.offEvent('themeChanged', onThemeChanged);
            tg.offEvent('viewportChanged', onViewportChanged);
        };
    }, []);

    // Handle Telegram back button
    useEffect(() => {
        const tg = window.Telegram?.WebApp;
        if (!tg) return;

        const handleBack = () => setCurrentPage('main');

        if (currentPage !== 'main') {
            tg.BackButton.show();
            tg.BackButton.onClick(handleBack);
        } else {
            tg.BackButton.hide();
        }

        return () => {
            tg.BackButton.offClick(handleBack);
        };
    }, [currentPage]);

    useEffect(() => {
        if (wallet) {
            setUser({
                account: wallet.account,
                publicKey: wallet.publicKey,
            });
        } else {
            setUser(null);
        }
    }, [wallet]);

    const handleNavigate = (page) => {
        if (page === 'exit') {
            setCurrentPage('main');
        } else if (page === 'create' || page === 'contracts' || page === 'readme' || page === 'sweep') {
            setCurrentPage(page);
        }
    };

    //  pending      
    const handleContractCreated = (pendingContract) => {
        setPendingContracts(prev => [...prev, pendingContract]);
        setCurrentPage('contracts');
    };

    //  pending  (   )
    const handlePendingContractResolved = (pendingId) => {
        setPendingContracts(prev => prev.filter(p => p.id !== pendingId));
    };

    //  
    if (currentPage === 'main') {
        return <MainPage user={user} onNavigate={handleNavigate} />;
    }

    //  Readme
    if (currentPage === 'readme') {
        return <Readme onBack={() => setCurrentPage('main')} />;
    }

    //   
    if (currentPage === 'create') {
        return (
            <CreateContract 
                user={user} 
                onBack={() => setCurrentPage('main')}
                onContractCreated={handleContractCreated}
            />
        );
    }

    //  Sweep
    if (currentPage === 'sweep') {
        return (
            <Sweep
                user={user}
                onBack={() => setCurrentPage('main')}
            />
        );
    }

    //    
    return (
        <ContractsList 
            user={user} 
            onBack={() => setCurrentPage('main')}
            pendingContracts={pendingContracts}
            onPendingResolved={handlePendingContractResolved}
        />
    );
}

export default App;
