import React, { useMemo } from 'react';
import { TonConnectUIProvider } from '@tonconnect/ui-react';
import { CONFIG } from './config';

const isTWA = typeof window !== 'undefined' && !!window.Telegram?.WebApp?.initData;

function TonConnectProvider({ children }) {
    const actionsConfiguration = useMemo(() => {
        if (isTWA) {
            return {
                twaReturnUrl: window.location.origin,
            };
        }
        return {
            returnStrategy: 'back',
            skipRedirectToWallet: 'always',
        };
    }, []);

    return (
        <TonConnectUIProvider
            manifestUrl={CONFIG.TONCONNECT_MANIFEST_URL}
            actionsConfiguration={actionsConfiguration}
        >
            {children}
        </TonConnectUIProvider>
    );
}

export default TonConnectProvider;
