// Function to convert technical errors into user-friendly messages
const getUserFriendlyError = (error, t) => {
    const errorMessage = error?.message || error?.toString() || '';
    const errorString = errorMessage.toLowerCase();
    
    if (errorString.includes('user reject') || 
        errorString.includes('user cancel') ||
        errorString.includes('rejected') ||
        errorString.includes('cancelled by user')) {
        return t('err_cancelled');
    }
    
    if (errorString.includes('insufficient') || 
        errorString.includes('not enough') ||
        errorString.includes('balance')) {
        return t('err_balance');
    }
    
    if (errorString.includes('timeout') || 
        errorString.includes('timed out')) {
        return t('err_timeout');
    }
    
    if (errorString.includes('network') || 
        errorString.includes('connection') ||
        errorString.includes('fetch failed')) {
        return t('err_network');
    }
    
    if (errorString.includes('contract not found') || 
        errorString.includes('account not found') ||
        errorString.includes('error code: -13')) {
        return t('err_factory');
    }
    
    if (errorString.includes('not initialized') ||
        errorString.includes('')) {
        return t('err_not_ready');
    }
    
    if (errorString.includes('gas') || 
        errorString.includes('fee')) {
        return t('err_gas');
    }
    
    if (errorMessage.length < 100 && !errorString.includes('error') && !errorString.includes('failed')) {
        return errorMessage;
    }
    
    return t('err_generic');
};

// Date formatting: dots instead of slashes, timezone after PM
const formatContractDate = (date) => {
    return new Date(date).toLocaleString('en-US', {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
        hour: 'numeric',
        minute: '2-digit',
        second: '2-digit',
        hour12: true,
        timeZoneName: 'shortOffset'
    }).replace(/\//g, '.').replace(/\bGMT\b/g, '');
};

// Address normalization function for comparison
const normalizeAddress = (addressInput) => {
    if (!addressInput) return null;
    try {
        // If already an Address object, use it directly
        if (typeof addressInput === 'object' && typeof addressInput.toRawString === 'function') {
            return addressInput.toRawString();
        }
        // If a string - parse it
        const addr = Address.parse(addressInput);
        return addr.toRawString();
    } catch {
        return typeof addressInput === 'string' ? addressInput : null;
    }
};

// Address comparison function
const addressEquals = (addr1, addr2) => {
    if (!addr1 || !addr2) return false;
    return normalizeAddress(addr1) === normalizeAddress(addr2);
};

