/* eslint-env es2020 */
/* global BigInt */
import React, { useState, useEffect, useCallback } from 'react';
import { Address, beginCell, Cell, toNano } from '@ton/core';
import { useTonConnectUI } from '@tonconnect/ui-react';
import { CONFIG } from '../config';
import { useLanguage } from '../LangContext';
import './Sweep.css';

const FACTORY_ADDRESSES = [
    'EQAwQR_gxPVUsLM3CHC2sA3UGZgozQ0Kq-S7Atmm5wTuR54c',
];

const normalizeAddr = (addr) => addr.replace(/-/g, '+').replace(/_/g, '/');
const parseAddr = (addr) => Address.parse(normalizeAddr(addr));

function Sweep({ user, onBack }) {
    const { t } = useLanguage();
    const [tonConnectUI] = useTonConnectUI();
    const [contracts, setContracts] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [sweeping, setSweeping] = useState(new Map());
    const [sweepAllInProgress, setSweepAllInProgress] = useState(false);
    const [manualAddress, setManualAddress] = useState('');
    const [addingManual, setAddingManual] = useState(false);
    const [scanStatus, setScanStatus] = useState('');

    const apiCall = useCallback(async (method, params) => {
        for (let attempt = 0; attempt < 5; attempt++) {
            const response = await fetch(CONFIG.TON_API_URL, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ id: 1, jsonrpc: '2.0', method, params }),
            });
            if (response.status === 429) {
                await new Promise(r => setTimeout(r, 1500 * (attempt + 1)));
                continue;
            }
            return response;
        }
        return null;
    }, []);

    const fetchContractData = useCallback(async (contractAddress) => {
        try {
            const response = await apiCall('runGetMethod', { address: contractAddress, method: 'getContractData', stack: [] });
            if (!response) return null;
            const data = await response.json();
            if (!data.result || (data.result.exit_code !== 0 && data.result.exit_code !== null)) return null;

            const stack = data.result.stack;
            if (!stack || stack.length < 10) return null;

            const parseValue = (item) => {
                if (Array.isArray(item)) {
                    const v = item[1];
                    if (typeof v === 'string') { try { return BigInt(v); } catch { return BigInt(0); } }
                    if (typeof v === 'number') return BigInt(v);
                }
                return BigInt(0);
            };
            const parseBool = (item) => {
                if (Array.isArray(item) && item.length >= 2) {
                    return item[1] !== '0x0' && item[1] !== '0' && item[1] !== 0;
                }
                return false;
            };
            const parseAddress = (item) => {
                try {
                    if (Array.isArray(item) && (item[0] === 'cell' || item[0] === 'tvm.Cell')) {
                        const cellData = item[1]?.bytes || item[1];
                        if (cellData) {
                            const cell = Cell.fromBase64(cellData);
                            return cell.beginParse().loadAddress();
                        }
                    }
                } catch {}
                return null;
            };

            let contractName = '';
            try {
                const nameItem = stack[3];
                if (nameItem && Array.isArray(nameItem)) {
                    const cellData = nameItem[1]?.bytes || nameItem[1];
                    if (cellData) contractName = Cell.fromBase64(cellData).beginParse().loadStringTail();
                }
            } catch {}

            return {
                active: parseBool(stack[0]),
                owner: parseAddress(stack[1]),
                name: contractName || 'Unnamed',
                targetAmount: parseValue(stack[4]),
                depositAmount: parseValue(stack[5]),
                interval: parseValue(stack[6]),
                intervalEndTime: parseValue(stack[7]),
                totalDeposits: parseValue(stack[8]),
                createdAt: parseValue(stack[9]),
                isJetton: stack.length >= 13 ? parseBool(stack[12]) : false,
                jettonMaster: stack.length >= 13 ? parseAddress(stack[10]) : null,
            };
        } catch (err) {
            console.error('Error fetching contract data:', contractAddress, err);
            return null;
        }
    }, [apiCall]);

    const isSweepable = (cd) => {
        if (!cd || !cd.active) return false;
        const now = Math.floor(Date.now() / 1000);
        const deadline = Number(cd.intervalEndTime);
        return deadline > 0 && deadline < now && cd.totalDeposits < cd.targetAmount;
    };

    const addUniqueAddress = (list, addr) => {
        try {
            const raw = parseAddr(addr).toRawString();
            if (!list.some(a => { try { return parseAddr(a).toRawString() === raw; } catch { return false; } })) {
                list.push(parseAddr(addr).toString());
            }
        } catch {}
    };

    const getChildAddressesFromTransactions = async (factoryAddr) => {
        const addresses = [];
        let lastLt, lastHash;
        const factoryRaw = parseAddr(factoryAddr).toRawString();
        const userRaw = user?.account?.address ? 
            (() => { try { return parseAddr(user.account.address).toRawString(); } catch { return null; } })() : null;

        for (let page = 0; page < 10; page++) {
            const params = { address: factoryAddr, limit: 100 };
            if (lastLt) { params.lt = lastLt; params.hash = lastHash; }

            const response = await apiCall('getTransactions', params);
            if (!response) break;
            const data = await response.json();
            const txs = data.result || [];
            if (txs.length === 0) break;

            for (const tx of txs) {
                if (!tx.out_msgs) continue;
                for (const msg of tx.out_msgs) {
                    if (!msg.destination) continue;
                    try {
                        const destRaw = parseAddr(msg.destination).toRawString();
                        if (destRaw !== factoryRaw && destRaw !== userRaw) {
                            addUniqueAddress(addresses, msg.destination);
                        }
                    } catch {}
                }
            }

            if (txs.length < 100) break;
            const last = txs[txs.length - 1];
            lastLt = last.transaction_id?.lt;
            lastHash = last.transaction_id?.hash;
            if (!lastLt) break;
            await new Promise(r => setTimeout(r, 1200));
        }
        return addresses;
    };

    const getChildAddressesFromGetArray = async (factoryAddr, userAddress) => {
        const addresses = [];
        try {
            const userAddr = parseAddr(userAddress);
            const addressCell = beginCell().storeAddress(userAddr).endCell();

            const response = await apiCall('runGetMethod', {
                address: factoryAddr,
                method: 'getArray',
                stack: [['tvm.Slice', addressCell.toBoc().toString('base64')]],
            });
            if (!response) return addresses;
            const data = await response.json();
            if (!data.result || (data.result.exit_code !== 0 && data.result.exit_code !== null)) return addresses;

            const stack = data.result.stack;
            if (!stack || stack.length < 2) return addresses;

            const countItem = stack[0];
            let count = 0;
            if (Array.isArray(countItem) && countItem[0] === 'num') {
                count = parseInt(countItem[1], 16) || parseInt(countItem[1]) || 0;
            }
            if (count === 0) return addresses;

            const cellItem = stack[1];
            if (!cellItem || !Array.isArray(cellItem)) return addresses;
            const cellData = cellItem[0] === 'cell' ? (cellItem[1]?.bytes || cellItem[1]) : null;
            if (!cellData) return addresses;

            const dictCell = Cell.fromBase64(cellData);
            const { Dictionary } = await import('@ton/core');
            const dict = dictCell.beginParse().loadDict(Dictionary.Keys.Int(257), Dictionary.Values.Address());
            for (let i = 0; i < count; i++) {
                const addr = dict.get(i);
                if (addr) addUniqueAddress(addresses, addr.toString());
            }
        } catch (err) {
            console.warn(`getArray failed for factory ${factoryAddr}:`, err);
        }
        return addresses;
    };

    const loadSweepableContracts = useCallback(async () => {
        setLoading(true);
        setError(null);
        setScanStatus('');

        try {
            let allAddresses = [];

            for (const factoryAddr of FACTORY_ADDRESSES) {
                setScanStatus(`Scanning factory ${factoryAddr.slice(0, 8)}... (transactions)`);
                const txAddrs = await getChildAddressesFromTransactions(factoryAddr);
                txAddrs.forEach(a => addUniqueAddress(allAddresses, a));
                console.log(`Factory ${factoryAddr.slice(0, 8)}: ${txAddrs.length} from transactions`);
                await new Promise(r => setTimeout(r, 1500));

                if (user?.account?.address) {
                    setScanStatus(`Scanning factory ${factoryAddr.slice(0, 8)}... (getArray)`);
                    const arrayAddrs = await getChildAddressesFromGetArray(factoryAddr, user.account.address);
                    arrayAddrs.forEach(a => addUniqueAddress(allAddresses, a));
                    console.log(`Factory ${factoryAddr.slice(0, 8)}: ${arrayAddrs.length} from getArray`);
                }
            }

            console.log(`Total unique child contracts: ${allAddresses.length}`);
            await new Promise(r => setTimeout(r, 2000));
            setScanStatus(`Checking ${allAddresses.length} contracts...`);

            const sweepable = [];
            const now = Math.floor(Date.now() / 1000);

            for (let i = 0; i < allAddresses.length; i++) {
                setScanStatus(`Checking contract ${i + 1}/${allAddresses.length}...`);
                const cd = await fetchContractData(allAddresses[i]);
                if (!cd) { await new Promise(r => setTimeout(r, 1200)); continue; }
                const deadline = Number(cd.intervalEndTime);
                const deadlinePassed = deadline > 0 && deadline < now;
                const targetReached = cd.totalDeposits >= cd.targetAmount;
                if (cd.active && deadlinePassed && !targetReached) {
                    sweepable.push({ address: allAddresses[i], ...cd });
                }
                if (i < allAddresses.length - 1) {
                    await new Promise(r => setTimeout(r, 1200));
                }
            }

            console.log(`Found ${sweepable.length} sweepable contracts`);
            setContracts(sweepable);
            setScanStatus('');
        } catch (err) {
            console.error('Error loading sweepable contracts:', err);
            setError(t('sweep_err_failed') + (err.message || 'Unknown error'));
        } finally {
            setLoading(false);
            setScanStatus('');
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [user, fetchContractData]);

    useEffect(() => {
        loadSweepableContracts();
    }, [loadSweepableContracts]);

    const handleAddManual = async () => {
        if (!manualAddress.trim()) return;
        setAddingManual(true);
        setError(null);

        try {
            const addr = parseAddr(manualAddress.trim()).toString();
            const existing = contracts.find(c => {
                try { return parseAddr(c.address).toRawString() === parseAddr(addr).toRawString(); }
                catch { return false; }
            });
            if (existing) { setError(t('sweep_err_exists')); setAddingManual(false); return; }

            const cd = await fetchContractData(addr);
            if (!cd) { setError(t('sweep_err_read')); setAddingManual(false); return; }
            if (!isSweepable(cd)) {
                const reason = !cd.active ? t('sweep_reason_inactive') :
                    Number(cd.intervalEndTime) > Math.floor(Date.now() / 1000) ? t('sweep_reason_deadline') :
                    t('sweep_reason_target');
                setError(t('sweep_err_not_sweepable', reason));
                setAddingManual(false);
                return;
            }

            setContracts(prev => [...prev, { address: addr, ...cd }]);
            setManualAddress('');
        } catch (err) {
            setError(t('sweep_err_invalid') + (err.message || 'Unknown error'));
        } finally {
            setAddingManual(false);
        }
    };

    const handleSweep = async (contractAddress) => {
        if (!user) return;
        setSweeping(prev => new Map(prev).set(contractAddress, true));
        try {
            const body = beginCell().storeUint(0, 32).storeStringTail("lose").endCell();
            await tonConnectUI.sendTransaction({
                messages: [{
                    address: parseAddr(contractAddress).toString(),
                    amount: toNano('0.05').toString(),
                    payload: body.toBoc().toString('base64'),
                }],
                validUntil: Math.floor(Date.now() / 1000) + 300,
            });
            setContracts(prev => prev.filter(c => c.address !== contractAddress));
        } catch (err) {
            console.error('Sweep error:', err);
            if (!err.message?.includes('User rejected') && !err.message?.includes('Cancelled')) {
                setError(t('sweep_err_sweep') + (err.message || 'Unknown error'));
            }
        } finally {
            setSweeping(prev => { const m = new Map(prev); m.delete(contractAddress); return m; });
        }
    };

    const handleSweepAll = async () => {
        if (!user || contracts.length === 0) return;
        setSweepAllInProgress(true);
        setError(null);
        try {
            const messages = contracts.map(c => {
                const body = beginCell().storeUint(0, 32).storeStringTail("lose").endCell();
                return {
                    address: parseAddr(c.address).toString(),
                    amount: toNano('0.05').toString(),
                    payload: body.toBoc().toString('base64'),
                };
            });
            const MAX_PER_TX = 4;
            for (let i = 0; i < messages.length; i += MAX_PER_TX) {
                await tonConnectUI.sendTransaction({
                    messages: messages.slice(i, i + MAX_PER_TX),
                    validUntil: Math.floor(Date.now() / 1000) + 300,
                });
            }
            setContracts([]);
        } catch (err) {
            console.error('Sweep all error:', err);
            if (!err.message?.includes('User rejected') && !err.message?.includes('Cancelled')) {
                setError(t('sweep_err_sweep_all') + (err.message || 'Unknown error'));
            }
        } finally {
            setSweepAllInProgress(false);
        }
    };

    const formatAmount = (amount, decimals = 9) => {
        const val = Number(amount) / Math.pow(10, decimals);
        if (val >= 1000000) return (val / 1000000).toFixed(2) + 'M';
        if (val >= 1000) return (val / 1000).toFixed(2) + 'K';
        return val.toFixed(2);
    };

    const formatDate = (ts) => {
        if (!ts) return '';
        return new Date(Number(ts) * 1000).toLocaleDateString('en-US', {
            month: 'short', day: 'numeric', year: 'numeric',
            hour: '2-digit', minute: '2-digit',
        });
    };

    const shortAddr = (addr) => {
        const s = addr.toString();
        return s.slice(0, 6) + '...' + s.slice(-4);
    };

    return (
        <div className="sweep-page">
            <div className="sweep-header">
                <button onClick={onBack} className="back-button">
                    <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <polyline points="15 18 9 12 15 6"/>
                    </svg>
                    {t('back')}
                </button>
                <h1>{t('sweep_title')}</h1>
                <button onClick={loadSweepableContracts} className="refresh-button" disabled={loading}>
                    <svg width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2"
                         className={loading ? 'spinning' : ''}>
                        <path d="M23 4v6h-6"/><path d="M1 20v-6h6"/>
                        <path d="M3.51 9a9 9 0 0 1 14.85-3.36L23 10M1 14l4.64 4.36A9 9 0 0 0 20.49 15"/>
                    </svg>
                </button>
            </div>

            <div className="sweep-content">
                {error && <div className="sweep-error">{error}</div>}

                <div className="sweep-add-manual">
                    <input
                        type="text"
                        value={manualAddress}
                        onChange={(e) => setManualAddress(e.target.value)}
                        placeholder={t('sweep_placeholder')}
                        className="sweep-input"
                        onKeyDown={(e) => e.key === 'Enter' && handleAddManual()}
                    />
                    <button onClick={handleAddManual} disabled={addingManual || !manualAddress.trim()} className="sweep-add-button">
                        {addingManual ? t('sweep_adding') : t('sweep_add')}
                    </button>
                </div>

                {loading ? (
                    <div className="sweep-loading">
                        <div className="sweep-spinner"></div>
                        <p>{t('sweep_scanning')}</p>
                        {scanStatus && <small>{scanStatus}</small>}
                    </div>
                ) : contracts.length === 0 ? (
                    <div className="sweep-empty">
                        <svg width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5">
                            <path d="M9 12l2 2 4-4"/>
                            <circle cx="12" cy="12" r="10"/>
                        </svg>
                        <p>{t('sweep_empty')}</p>
                        <small>{t('sweep_empty_hint')}</small>
                    </div>
                ) : (
                    <>
                        <div className="sweep-summary">
                            <span>{t('sweep_summary', contracts.length)}</span>
                            <button
                                className="sweep-all-button"
                                onClick={handleSweepAll}
                                disabled={sweepAllInProgress}
                            >
                                {sweepAllInProgress ? t('sweep_all_ing') : t('sweep_all', contracts.length)}
                            </button>
                        </div>

                        <div className="sweep-list">
                            {contracts.map(c => (
                                <div key={c.address} className="sweep-card">
                                    <div className="sweep-card-header">
                                        <span className="sweep-card-name">{c.name}</span>
                                        <span className="sweep-card-badge">{t('sweep_expired')}</span>
                                    </div>
                                    <div className="sweep-card-details">
                                        <div className="sweep-card-row">
                                            <span className="sweep-card-label">{t('sweep_address')}</span>
                                            <span className="sweep-card-value">{shortAddr(c.address)}</span>
                                        </div>
                                        <div className="sweep-card-row">
                                            <span className="sweep-card-label">{t('sweep_owner')}</span>
                                            <span className="sweep-card-value">{c.owner ? shortAddr(c.owner) : ''}</span>
                                        </div>
                                        <div className="sweep-card-row">
                                            <span className="sweep-card-label">{t('sweep_progress')}</span>
                                            <span className="sweep-card-value">
                                                {formatAmount(c.totalDeposits)} / {formatAmount(c.targetAmount)}
                                                {c.isJetton ? ' (jetton)' : ' TON'}
                                            </span>
                                        </div>
                                        <div className="sweep-card-row">
                                            <span className="sweep-card-label">{t('sweep_deadline')}</span>
                                            <span className="sweep-card-value sweep-card-expired">
                                                {formatDate(c.intervalEndTime)}
                                            </span>
                                        </div>
                                    </div>
                                    <button
                                        className="sweep-card-button"
                                        onClick={() => handleSweep(c.address)}
                                        disabled={sweeping.get(c.address)}
                                    >
                                        {sweeping.get(c.address) ? t('sweep_btn_ing') : t('sweep_btn')}
                                    </button>
                                </div>
                            ))}
                        </div>
                    </>
                )}
            </div>
        </div>
    );
}

export default Sweep;
