import React, { useMemo } from 'react';
import { TonConnectButton } from '@tonconnect/ui-react';
import { Address } from '@ton/core';
import { useLanguage } from '../LangContext';
import './MainPage.css';

const SWEEP_ALLOWED_ADDRESS = 'UQCpGc70M4JGCW4CxdoOCV95OwmkwaJzybpBmIfdTWRB2FFi';

function MainPage({ user, onNavigate }) {
    const { lang, toggle, t } = useLanguage();
    const showSweep = useMemo(() => {
        if (!user?.account?.address) return false;
        try {
            const connected = Address.parse(user.account.address).toRawString();
            const allowed = Address.parse(SWEEP_ALLOWED_ADDRESS).toRawString();
            return connected === allowed;
        } catch { return false; }
    }, [user]);
    return (
        <div className="main-page">
            <header className="main-header">
                <div className="header-left">
                <button className="lang-toggle" onClick={toggle} title="Switch language">
                        {lang === 'en' ? 'RU' : 'EN'}
                    </button>
                </div>
                <div className="header-right">

                    <TonConnectButton />
                </div>
            </header>

            <main className="main-content">
                <div className="main-logo">
                    <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5">
                        <path d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                        <path d="M9 12l2 2 4-4" />
                    </svg>
                </div>
                <h1 className="main-title">Escrow delays</h1>
                <p className="main-subtitle">{t('main_subtitle')}</p>
                
                <div className="main-actions">
                    <button 
                        className="action-button primary"
                        onClick={() => onNavigate('create')}
                    >
                        <span className="action-button-icon">
                            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <line x1="12" y1="5" x2="12" y2="19"/>
                                <line x1="5" y1="12" x2="19" y2="12"/>
                            </svg>
                        </span>
                        {t('main_create')}
                    </button>
                    <button 
                        className="action-button"
                        onClick={() => onNavigate('contracts')}
                    >
                        <span className="action-button-icon">
                            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <rect x="3" y="3" width="7" height="7" rx="1"/>
                                <rect x="14" y="3" width="7" height="7" rx="1"/>
                                <rect x="3" y="14" width="7" height="7" rx="1"/>
                                <rect x="14" y="14" width="7" height="7" rx="1"/>
                            </svg>
                        </span>
                        {t('main_contracts')}
                    </button>
                    {showSweep && (
                    <button 
                        className="action-button sweep"
                        onClick={() => onNavigate('sweep')}
                    >
                        <span className="action-button-icon">
                            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                                <path d="M3 6h18"/>
                                <path d="M8 6V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"/>
                                <path d="M19 6l-1 14a2 2 0 0 1-2 2H8a2 2 0 0 1-2-2L5 6"/>
                                <line x1="10" y1="11" x2="10" y2="17"/>
                                <line x1="14" y1="11" x2="14" y2="17"/>
                            </svg>
                        </span>
                        Sweep
                    </button>
                    )}
                </div>
            </main>

            <footer className="main-footer">
                <p>
                    <a href="https://escrowdelays.com" target="_blank" rel="noopener noreferrer" 
                    style={{textDecoration: 'none'}} >
                        escrowdelays.com
                    </a>
                </p>
            </footer>
        </div>
    );
}

export default MainPage;
