/* global BigInt */
import React, { useState, useEffect } from 'react';
import { Address, toNano, beginCell, Cell, Dictionary } from '@ton/core';
import { useTonConnectUI } from '@tonconnect/ui-react';
import { CONFIG } from '../config';
import { useLanguage } from '../LangContext';
import './CreateContract.css';

// Popular jettons in testnet/mainnet
const POPULAR_JETTONS = {
    testnet: [
        { symbol: 'TON', address: null, name: 'Native TON', decimals: 9 },
        { symbol: 'USDT', address: 'kQB0ZYUL5M3KfrW0tSnwdFO1nC-BQHC2gcZl-WaF2on_USDT', name: 'My Test USDT', decimals: 6 },
    ],
    mainnet: [
        { symbol: 'TON', address: null, name: 'Native TON', decimals: 9 },
        { symbol: 'USDT', address: 'EQCxE6mUtQJKFnGfaROTKOt1lZbDiiX1kCixRv7Nw2Id_sDs', name: 'Tether USD', decimals: 6 },
    ]
};

// SHA256 hashes of metadata keys (TEP-64)
const METADATA_KEYS = {
    decimals: 'ee80fd2f1e03480e2282363596ee752d7bb27f50776b95086a0279189675923e',
    name: '82a3537ff0dbce7eec35d69edc3a189ee6f17d82f353a553f9aa96cb0be3ce89',
    symbol: 'b76a7ca153c24671658335bbd08946350ffc621fa1c516e7123095d4ffd5c581',
    image: '6105d6cc76af400325e94d588ce511be5bfdbb73b437dc51eca43917d7a43e3d',
};

// Function to fetch jetton metadata from contract
async function fetchJettonMetadata(jettonMasterAddress, apiUrl) {
    try {
        const response = await fetch(apiUrl, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                id: 1,
                jsonrpc: '2.0',
                method: 'runGetMethod',
                params: {
                    address: jettonMasterAddress,
                    method: 'get_jetton_data',
                    stack: [],
                },
            }),
        });

        const data = await response.json();
        
        if (data.error || !data.result || data.result.exit_code !== 0) {
            console.warn('Failed to get jetton data:', data.error || data.result);
            return null;
        }

        const stack = data.result.stack;
        if (!stack || stack.length < 4) {
            console.warn('Invalid jetton data stack');
            return null;
        }

        const contentItem = stack[3];
        if (!contentItem || !Array.isArray(contentItem)) {
            return null;
        }

        const cellData = contentItem[1]?.bytes || contentItem[1];
        if (!cellData) {
            return null;
        }

        const contentCell = Cell.fromBase64(cellData);
        const slice = contentCell.beginParse();
        
        const contentType = slice.loadUint(8);
        
        let metadata = { decimals: 9, symbol: 'JETTON', name: 'Unknown Jetton' };
        
            if (contentType === 0x00) {
            // On-chain metadata (Dictionary)
            try {
                const dict = slice.loadDict(Dictionary.Keys.Buffer(32), Dictionary.Values.Cell());
                
                const decimalsKey = Buffer.from(METADATA_KEYS.decimals, 'hex');
                const decimalsCell = dict.get(decimalsKey);
                if (decimalsCell) {
                    const decimalsSlice = decimalsCell.beginParse();
                    decimalsSlice.loadUint(8);
                    const decimalsStr = decimalsSlice.loadStringTail();
                    metadata.decimals = parseInt(decimalsStr) || 9;
                }
                
                const symbolKey = Buffer.from(METADATA_KEYS.symbol, 'hex');
                const symbolCell = dict.get(symbolKey);
                if (symbolCell) {
                    const symbolSlice = symbolCell.beginParse();
                    symbolSlice.loadUint(8);
                    metadata.symbol = symbolSlice.loadStringTail() || 'JETTON';
                }
                
                const nameKey = Buffer.from(METADATA_KEYS.name, 'hex');
                const nameCell = dict.get(nameKey);
                if (nameCell) {
                    const nameSlice = nameCell.beginParse();
                    nameSlice.loadUint(8);
                    metadata.name = nameSlice.loadStringTail() || 'Unknown Jetton';
                }
            } catch (e) {
                console.warn('Error parsing on-chain metadata:', e);
            }
        } else if (contentType === 0x01) {
            // Off-chain metadata (URL)
            try {
                const url = slice.loadStringTail();
                if (url.startsWith('http://') || url.startsWith('https://')) {
                    try {
                        const metaResponse = await fetch(url);
                        const metaJson = await metaResponse.json();
                        
                        if (metaJson.decimals !== undefined) {
                            metadata.decimals = parseInt(metaJson.decimals) || 9;
                        }
                        if (metaJson.symbol) {
                            metadata.symbol = metaJson.symbol;
                        }
                        if (metaJson.name) {
                            metadata.name = metaJson.name;
                        }
                    } catch (fetchErr) {
                        console.warn('Failed to fetch off-chain metadata:', fetchErr);
                    }
                }
            } catch (e) {
                console.warn('Error parsing off-chain metadata URL:', e);
            }
        }
        
        return metadata;
    } catch (err) {
        console.error('Error fetching jetton metadata:', err);
        return null;
    }
}

function ContractAddressBlock({ address }) {
    const [copied, setCopied] = useState(false);
    const handleCopy = () => {
        navigator.clipboard.writeText(address);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
    };
    return (
        <div
            className="paper"
            style={{ marginTop: 10, cursor: 'pointer', display: 'flex', alignItems: 'center', justifyContent: 'space-between', gap: 8 }}
            onClick={handleCopy}
            title="Copy contract address"
        >
            <span style={{ fontSize: 12, wordBreak: 'break-all', fontFamily: 'monospace', opacity: 0.8 }}>
                {address}
            </span>
            <span style={{ flexShrink: 0, fontSize: 12, opacity: copied ? 1 : 0.5 }}>
                {copied ? (
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round"><polyline points="20 6 9 17 4 12"/></svg>
                ) : (
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><rect x="9" y="9" width="13" height="13" rx="2" ry="2"/><path d="M5 15H4a2 2 0 0 1-2-2V4a2 2 0 0 1 2-2h9a2 2 0 0 1 2 2v1"/></svg>
                )}
            </span>
        </div>
    );
}

function CreateContract({ user, onBack, onContractCreated }) {
    const { t } = useLanguage();
    const [tonConnectUI] = useTonConnectUI();
    const [arrayAddress, setArrayAddress] = useState('');
    const [contractType, setContractType] = useState('array');
    const [configLoaded, setConfigLoaded] = useState(false);
    const [adding, setAdding] = useState(false);
    const [error, setError] = useState(null);
    
    // Contract creation parameters
    const [contractName, setContractName] = useState('');
    const [targetAmount, setTargetAmount] = useState('');
    const [regularDeposite, setRegularDeposite] = useState('');
    const [interval, setIntervalValue] = useState('');
    
    // Jetton parameters
    const [selectedJetton, setSelectedJetton] = useState('TON');
    const [customJettonAddress, setCustomJettonAddress] = useState('');
    const [useCustomJetton, setUseCustomJetton] = useState(false);
    const [customJettonMetadata, setCustomJettonMetadata] = useState(null);
    const [loadingJettonMetadata, setLoadingJettonMetadata] = useState(false);
    const [jettonMetadataCache, setJettonMetadataCache] = useState(new Map());
    const [jettonValidation, setJettonValidation] = useState({ valid: null, error: null }); // Jetton Master validation

    const jettonsList = POPULAR_JETTONS[CONFIG.NETWORK] || POPULAR_JETTONS.testnet;

    useEffect(() => {
        const FALLBACK_ADDRESS = 'EQAwQR_gxPVUsLM3CHC2sA3UGZgozQ0Kq-S7Atmm5wTuR54c';
        const FALLBACK_TYPE = 'arrayJetton';

        const loadConfig = async () => {
            try {
                const response = await fetch('/array-config.json');
                if (response.ok) {
                    const config = await response.json();
                    if (config?.arrayAddress) setArrayAddress(config.arrayAddress);
                    else setArrayAddress(FALLBACK_ADDRESS);
                    if (config?.contractType) setContractType(config.contractType);
                    else setContractType(FALLBACK_TYPE);
                } else {
                    setArrayAddress(FALLBACK_ADDRESS);
                    setContractType(FALLBACK_TYPE);
                }
            } catch (e) {
                console.warn('Could not load array config, using fallback:', e);
                setArrayAddress(FALLBACK_ADDRESS);
                setContractType(FALLBACK_TYPE);
            }
            setConfigLoaded(true);
        };
        loadConfig();
    }, []);

    // Load jetton metadata
    useEffect(() => {
        const loadMetadata = async () => {
            if (!useCustomJetton || !customJettonAddress) {
                setCustomJettonMetadata(null);
                return;
            }
            
            try {
                Address.parse(customJettonAddress);
            } catch {
                setCustomJettonMetadata(null);
                return;
            }
            
            if (jettonMetadataCache.has(customJettonAddress)) {
                setCustomJettonMetadata(jettonMetadataCache.get(customJettonAddress));
                return;
            }
            
            setLoadingJettonMetadata(true);
            try {
                const metadata = await fetchJettonMetadata(customJettonAddress, CONFIG.TON_API_URL);
                if (metadata) {
                    setCustomJettonMetadata(metadata);
                    setJettonMetadataCache(prev => new Map(prev).set(customJettonAddress, metadata));
                } else {
                    setCustomJettonMetadata({ decimals: 9, symbol: 'JETTON', name: 'Unknown' });
                }
            } catch (err) {
                console.error('Error loading jetton metadata:', err);
                setCustomJettonMetadata({ decimals: 9, symbol: 'JETTON', name: 'Unknown' });
            } finally {
                setLoadingJettonMetadata(false);
            }
        };
        
        const timeoutId = setTimeout(loadMetadata, 500);
        return () => clearTimeout(timeoutId);
    }, [useCustomJetton, customJettonAddress, jettonMetadataCache]);

    const getSelectedJettonMaster = () => {
        if (useCustomJetton && customJettonAddress) {
            return customJettonAddress;
        }
        const jetton = jettonsList.find(j => j.symbol === selectedJetton);
        return jetton ? jetton.address : null;
    };

    // Get current contract count from blockchain
    const getCurrentContractCount = async () => {
        if (!user) return 0;
        
        try {
            const userAddress = Address.parse(user.account.address);
            const addressCell = beginCell()
                .storeAddress(userAddress)
                .endCell();
            
            const response = await fetch(CONFIG.TON_API_URL, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    id: 1,
                    jsonrpc: '2.0',
                    method: 'runGetMethod',
                    params: {
                        address: arrayAddress,
                        method: 'getArray',
                        stack: [
                            ['tvm.Slice', addressCell.toBoc().toString('base64')],
                        ],
                    },
                }),
            });
            const data = await response.json();
            console.log('getCurrentContractCount response:', data);
            
            if (data.result && (data.result.exit_code === 0 || data.result.exit_code === null) && data.result.stack) {
                const stack = data.result.stack;
                // First element - contract count
                if (stack.length > 0) {
                    const countItem = stack[0];
                    let count = 0;
                    if (Array.isArray(countItem) && countItem[0] === 'num') {
                        count = parseInt(countItem[1], 16);
                    } else if (typeof countItem === 'number') {
                        count = countItem;
                    }
                    console.log('Current contract count:', count);
                    return count;
                }
            }
            return 0;
        } catch (err) {
            console.error('Error getting contract count:', err);
            return 0;
        }
    };

    const handleCreateContract = async () => {
        if (!user || !arrayAddress) {
            setError(t('create_err_wallet'));
            return;
        }

        const targetAmountNum = parseFloat(targetAmount);
        const regularDepositeNum = parseFloat(regularDeposite);
        const intervalNum = parseInt(interval);

        if (!targetAmount || isNaN(targetAmountNum) || targetAmountNum <= 0) {
            setError(t('create_err_target'));
            return;
        }

        if (!regularDeposite || isNaN(regularDepositeNum) || regularDepositeNum <= 0) {
            setError(t('create_err_deposit'));
            return;
        }

        if (!interval || isNaN(intervalNum) || intervalNum <= 0) {
            setError(t('create_err_interval'));
            return;
        }

        setAdding(true);
        setError(null);

        try {
            // Get current contract count BEFORE creation
            const initialContractCount = await getCurrentContractCount();
            console.log('Initial contract count before creation:', initialContractCount);
            
            const contractAddr = Address.parse(arrayAddress);
            const supportsJettons = contractType === 'arrayJetton';
            
            const jettonMasterAddress = getSelectedJettonMaster();
            const isJettonMode = jettonMasterAddress !== null;
            
            if (isJettonMode && !supportsJettons) {
                setError(t('create_err_no_jetton'));
                setAdding(false);
                return;
            }
            
            let decimals = 9;
            let tokenSymbol = 'TON';
            if (isJettonMode) {
                if (useCustomJetton && customJettonMetadata) {
                    decimals = customJettonMetadata.decimals;
                    tokenSymbol = customJettonMetadata.symbol;
                } else {
                    const jetton = jettonsList.find(j => j.address === jettonMasterAddress);
                    decimals = jetton ? jetton.decimals : 9;
                    tokenSymbol = jetton ? jetton.symbol : 'JETTON';
                }
            }
            
            const multiplier = Math.pow(10, decimals);
            const targetAmountBase = BigInt(Math.floor(targetAmountNum * multiplier));
            const regularDepositeBase = BigInt(Math.floor(regularDepositeNum * multiplier));
            const intervalValue = BigInt(intervalNum);
            const nameValue = contractName || 'Unnamed';
            
            let body;
            if (supportsJettons) {
                const CREATE_JETTON_CONTRACT_OPCODE = 0x762ae1eb;
                body = beginCell()
                    .storeUint(CREATE_JETTON_CONTRACT_OPCODE, 32)
                    .storeStringRefTail(nameValue)
                    .storeUint(targetAmountBase, 64)
                    .storeUint(regularDepositeBase, 64)
                    .storeUint(intervalValue, 64)
                    .storeAddress(jettonMasterAddress ? Address.parse(jettonMasterAddress) : null)
                    .endCell();
            } else {
                const CREATE_CONTRACT_OPCODE = 0xe2178c0c;
                body = beginCell()
                    .storeUint(CREATE_CONTRACT_OPCODE, 32)
                    .storeStringRefTail(nameValue)
                    .storeUint(targetAmountBase, 64)
                    .storeUint(regularDepositeBase, 64)
                    .storeUint(intervalValue, 64)
                    .endCell();
            }
            
            const deployAmount = toNano('0.15');
            
            const result = await tonConnectUI.sendTransaction({
                messages: [
                    {
                        address: contractAddr.toString(),
                        amount: deployAmount.toString(),
                        payload: body.toBoc().toString('base64'),
                    },
                ],
                validUntil: Math.floor(Date.now() / 1000) + 300,
            });

            console.log('Transaction sent:', result);
            setError(null);
            
            // Create pending contract with preliminary data
            const pendingContract = {
                id: `pending_${Date.now()}`,
                name: nameValue,
                targetAmount: targetAmountBase.toString(),
                regularDeposite: regularDepositeBase.toString(),
                interval: intervalValue.toString(),
                tokenSymbol: isJettonMode ? tokenSymbol : 'TON',
                isJetton: isJettonMode,
                jettonMaster: jettonMasterAddress,
                decimals: decimals,
                createdAt: Math.floor(Date.now() / 1000),
                transactionBoc: result.boc,
                initialContractCount: initialContractCount, // Contract count BEFORE creation
                arrayAddress: arrayAddress, // Array address for verification
                userAddress: user.account.address, // User address for verification
            };
            
            // Clear form
            setContractName('');
            setTargetAmount('');
            setRegularDeposite('');
            setIntervalValue('');
            
            // Navigate to contracts page with skeleton
            if (onContractCreated) {
                onContractCreated(pendingContract);
            }
            
        } catch (err) {
            console.error('Error creating contract:', err);
            // If user cancelled the transaction, don't show error
            if (err.message?.includes('User rejected') || err.message?.includes('Cancelled')) {
                setError(t('create_err_cancelled'));
            } else {
                setError(t('create_err_generic') + (err.message || 'Unknown error'));
            }
        } finally {
            setAdding(false);
        }
    };

    const isDepositTooLarge =
        targetAmount !== '' && regularDeposite !== '' &&
        !isNaN(parseFloat(targetAmount)) && !isNaN(parseFloat(regularDeposite)) &&
        parseFloat(regularDeposite) > parseFloat(targetAmount);

    if (!configLoaded) {
        return <div className="create-contract-loading">Loading...</div>;
    }

    return (
        <div className="create-contract">
            <div className="create-contract-header">
                <button onClick={onBack} className="back-button">
                    <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                        <polyline points="15 18 9 12 15 6"/>
                    </svg>
                    {t('back')}
                </button>
                <h1>{t('create_title')}</h1>
            </div>

            <div className="create-contract-content">
                <div className="paper">
                    <p className="create-contract-description">{t('create_description')}</p>
                </div>
                <div className="contract-form">
                    {/* Token selection */}
                    <div className="token-selection">
                        <label className="form-label">
                            {t('create_token_label')}
                        </label>
                        <div className="token-buttons">
                            {jettonsList.map(jetton => (
                                <button
                                    key={jetton.symbol}
                                    onClick={() => {
                                        setSelectedJetton(jetton.symbol);
                                        setUseCustomJetton(false);
                                    }}
                                    className={`token-button ${selectedJetton === jetton.symbol && !useCustomJetton ? 'active' : ''}`}
                                >
                                    {jetton.symbol}
                                </button>
                            ))}
                            <button
                                onClick={() => setUseCustomJetton(true)}
                                className={`token-button ${useCustomJetton ? 'active' : ''}`}
                            >
                                OTHER
                            </button>
                        </div>
                        
                        {useCustomJetton && (
                            <>
                                <input
                                    type="text"
                                    value={customJettonAddress}
                                    onChange={(e) => setCustomJettonAddress(e.target.value)}
                                    placeholder="Jetton Master contract address (EQ...)"
                                    className="form-input"
                                    style={{marginTop: '20px', marginBottom:'10px'}}
                                />
                                {loadingJettonMetadata && (
                                    <div className="metadata-loading">
                                        {t('create_loading_meta')}
                                    </div>
                                )}
                                {customJettonMetadata && !loadingJettonMetadata && (
                                    <div className="metadata-info">
                                         <strong>{customJettonMetadata.name}</strong> ({customJettonMetadata.symbol})
                                        <br/>
                                        <span>{t('create_jetton_decimals')} {customJettonMetadata.decimals}</span>
                                    </div>
                                )}
                            </>
                        )}
                        
                        <small className="form-hint">
                            {selectedJetton === 'TON' && !useCustomJetton 
                                ? t('create_hint_ton')
                                : useCustomJetton && customJettonMetadata 
                                    ? t('create_hint_custom', customJettonMetadata.name, customJettonMetadata.decimals)
                                    : selectedJetton === 'USDT' && !useCustomJetton
                                        ? t('create_hint_usdt')
                                        : t('create_hint_jetton')}
                        </small>
                    </div>

                    <label className="form-label">
                        {t('create_name_label')}
                        <input
                            type="text"
                            value={contractName}
                            onChange={(e) => setContractName(e.target.value)}
                            placeholder=""
                            className="form-input"
                        />
                    </label>
                    
                    <label className="form-label">
                        {t('create_target_label')}
                        <p className='small-text'>{t('create_target_hint')}</p>
                        <input
                            type="number"
                            value={targetAmount}
                            onChange={(e) => setTargetAmount(e.target.value)}
                            placeholder="10"
                            min="0.000001"
                            step="0.1"
                            className="form-input"
                        />
                    </label>
                    
                    <label className="form-label">
                        {t('create_deposit_label')}
                        <p className='small-text'>{t('create_deposit_hint')}</p>
                        <input
                            type="number"
                            value={regularDeposite}
                            onChange={(e) => setRegularDeposite(e.target.value)}
                            placeholder="1"
                            min="0.000001"
                            step="0.1"
                            className="form-input"
                        />
                        {isDepositTooLarge && (
                            <small className="form-error">
                                {t('create_err_deposit_large')}
                            </small>
                        )}
                    </label>
                    
                    <label className="form-label">
                        {t('create_interval_label')}
                        <p className='small-text'>{t('create_interval_hint')}</p>
                        <input
                            type="number"
                            value={interval}
                            onChange={(e) => setIntervalValue(e.target.value)}
                            placeholder="3600"
                            min="1"
                            className="form-input"
                        />
                        <small className="form-hint">
                            {interval ? (() => {
                                const s = parseInt(interval);
                                const years   = Math.floor(s / (365 * 24 * 3600));
                                const months  = Math.floor((s % (365 * 24 * 3600)) / (30 * 24 * 3600));
                                const days    = Math.floor((s % (30 * 24 * 3600)) / (24 * 3600));
                                const hours   = Math.floor((s % (24 * 3600)) / 3600);
                                const minutes = Math.floor((s % 3600) / 60);
                                return ' ' + [
                                    years   ? `${years}y`   : '',
                                    months  ? `${months}mo` : '',
                                    days    ? `${days}d`    : '',
                                    hours   ? `${hours}h`   : '',
                                    minutes ? `${minutes}m` : '',
                                ].filter(Boolean).join(' ') || '< 1m';
                            })() : ''}
                        </small>
                    </label>
                </div>

                {error && (
                    <div className="error-message">
                        {error}
                    </div>
                )}

                <button
                    onClick={handleCreateContract}
                    disabled={adding || !arrayAddress || !targetAmount || !regularDeposite || !interval || isDepositTooLarge}
                    className="create-button"
                >
                    {adding ? t('create_btn_loading') : t('create_btn')}
                </button>
                <div className="paper" style={{marginTop: '30px'}}>
                    <p className="create-contract-description">
                        {t('create_description2')}
                    </p>
                </div>
                <ContractAddressBlock address="EQAwQR_gxPVUsLM3CHC2sA3UGZgozQ0Kq-S7Atmm5wTuR54c" />
            </div>
        </div>
    );
}

export default CreateContract;
